    "Home.trailer": "Bande annonce",
    "LandingPage.Title": "Information importante",
    "LandingPage.paragraph1":
      "Les comptitions prvues du 21 au 23 aot sont pour l'instant maintenues en attente des nouvelles mesures gouvernementales. Les quipes sont mobilises pour que tout se passe bien, les traceurs finalisent leurs circuits. Les inscriptions seront ouvertes  la fin du mois de juin. Nous vous attendons nombreux. A bientt.",
    "LandingPage.paragraph2": "Le directeur de course",
    "LandingPage.button": "Accder au site",
    "Lego.bacgroundParagraph": "Lg'O prcision",
    "Lego.title": "Lg'O prcision",
    "Lego.box0Title": "Disponibilit",
    "Lego.box0Paragraph":
      "Atelier pour tous mis en place uniquement le samedi et le dimanche",
    "Lego.box1Title": "Droulement",
    "Lego.box1Paragraph1":
      "Vous avez en main une vraie carte dorientation et vous devez retrouver les balises dans un milieu totalement en LEGO install sur une table de 2m.",
    "Lego.box1Paragraph2":
      "Vous devez,  laide de la carte, raliser un parcours dorientation classique, et retrouver les bonnes balises sur le terrain LEGO. Ceci dans le bon ordre et le plus vite possible.",
    "Lego.box1Paragraph3": "Un exemple de terrain en LEGO avec des balises :",
    "Lego.box1Paragraph4": "Et la carte qui correspond:",
    "Lego.box1Paragraph5":
      "Vous pointez le dpart avec votre propre puce (ou une puce prte). Ensuite, il vous faut choisir les bonnes balises sur le terrain  laide de la carte. Pour cela, il est possible de tourner autour de la table LEGO et sur chaque balise, un numro de poste est  reporter sur le carton de contrle  laide dun stylo (votre propre stylo ou un stylo prt). Vous pointez larrive pour finir la course. Les HD10 devront retrouver des animaux plutt que des balises.",
    "Lego.box2Title": "Le classement",
    "Lego.box2Paragraph":
      "Le classement de lpreuve se fait dabord par points puis par le temps. Chaque balise correcte marque 1 point. ",
    "Lego.box3Title": "Rcompenseset catgories",
    "Lego.box3Paragraph1": "HD10 / HD12/ HD14 / HD16 / HD18 / HD20+.",
    "Lego.box3Paragraph2": "Tous les moins de 10 ans auront le choix dun lot.",
    "Lego.box3Paragraph3":
      "Les 3 premiers HD12/14/16/18 auront le choix dun LEGO.",
    "Lego.box3Paragraph4":
      "Les 3 premiers HD20+ seront sur la photo pour la gloire.",
