    "Training.title": "Entrainements",
    "Training.box0Title1": "Informations",
    "Training.box01Paragraph1":
      "Les clubs Balise 77 et OPA Montigny vous proposent deux journes d'entrainement le mercredi 19 et le jeudi 20 aot 2020 de 13h  18H.",
    "Training.box01Paragraph2":
      "Un rseau de postes d'environ 30 balises de niveau noir  vert avec des balisettes sera pos sur les cartes de Poligny et de Bourron Marlotte. Les cartes seront  rcuprer  l'Ermitage de Franchard (arena de la MD et de la LD) le mercredi et le jeudi pendant les horaires d'entrainement.",
    "Training.box0Title2": "Tarifs",
    "Training.box02Paragraph11":
      "Le prix est de 4 euros par carte. Pour payer, vous pouvez faire un virement: ",
    "Training.box02Paragraph12": "IBAN : FR76 1010 7003 2700 1190 4923 684",
    "Training.box02Paragraph13": "BIC : BREDFRPPXXX",
    "Training.box02Paragraph14":
      "Vous pouvez aussi envoyer un chque  CDCO77 chez Celine Dalier 26 Hameau de Bessonville 77760 La Chapelle La Reine  en identiquant en rfrence 'Entrainements'.",
    "Training.box01Paragraph3":
      "Pour rserver vos cartes avant le 7 aout, voici le ",
    "Training.box01Paragraph4": "lien",
    "Training.box01Paragraph5":
      "ATTENTION CHANGEMENT DU LIEU DE L'ENTRAINEMENT 1 PREVU PAR BALISE 77",
