    "Accomodation.title": "Hbergements",
    "Accomodation.box1Title": "Locations",
    "Accomodation.box11Paragraph":
      "Il y a de nombreuses possibilits de location dhbergement de type gte aux alentours de Fontainebleau et dans le sud de la Seine et Marne.",
    "Accomodation.box12Paragraph":
      "La region est prise des grimpeurs et randonneurs, il est donc conseill de rserver un hbergement le plus tt possible pour tre sr davoir de la place.",
    "Accomodation.box13Paragraph":
      "Voici quelques sites web recensant des hbergements disponibles dans la region:",
    "Accomodation.box131Paragraph": " (Prs de Nemours)",
    "Accomodation.box14Paragraph":
      "Voici quelques sites web gnraux pour une recherche plus vaste:",
    "Accomodation.box2Title": "Campings",
    "Accomodation.box2Paragraph":
      "Il y a plusieurs campings autour de la fort de fontainebleau. Voici une liste non exhaustive des campings les plus populaires:",
    "Accomodation.box21Paragraph": " (prs de Montigny-sur-loing)",
    "Accomodation.box22Paragraph": " (prs de Noisy-sur-cole)",
    "Accomodation.box23Paragraph": " (prs de Malesherbes)",
    "Accomodation.box24Paragraph": " (prs de Veneux-les-Sablons)",
    "Accomodation.box3Title": "Office de tourisme",
    "Accomodation.paragraph":
      "Vous pouvez aussi consulter les sites des offices de tourisme de Fontainebleau, Melun et Nemours pour plus de renseignements sur les hbergements dans la rgion",
    "Accomodation.officeFontaineblau": "Office de tourisme de Fontainebleau",
    "Accomodation.officeMelun": "Office de tourisme de Melun",
    "Accomodation.officeNemours": "Office de tourisme de Nemours",
    "Accomodation.officeGatinais":
      "Office de tourisme du Gtinais Val-de-Loing",
    "Home.title": "Accueil",
    "Home.bacgroundParagraph":
      "3 jours de course d'orientation en fort de Fontainebleau et ses environs",
    "Home.baliseSpTitle": "Sprint",
    "Home.baliseSpParagraph1": "21",
    "Home.baliseSpParagraph2": "aot",
    "Home.baliseMdTitle": "MD",
    "Home.baliseMdParagraph1": "22",
    "Home.baliseMdParagraph2": "aot",
    "Home.baliseLdTitle": "LD",
    "Home.baliseLdParagraph1": "23",
    "Home.baliseLdParagraph2": "aot",
    "Home.box1Title": "Informations",
    "Home.box11Paragraph":
      "Les Nationales Nord Ouest 2020 de course dorientation auront lieu du 21 au 23 aot 2020 dans la fort de Fontainebleau. Ce long week-end regroupe trois courses comptant pour le classement national:",
    "Home.box121Paragraph":
      "Un Sprint Urbain le vendredi 21 aot aprs midi dans la ville de Chteau-Landon",
    "Home.box122Paragraph":
      "Une Moyenne Distance le samedi 22 aot aprs midi sur la carte de la Salamandre",
    "Home.box123Paragraph":
      "Une Longue Distance le dimache 23 aot matin sur la carte de Franchard",
    "Home.box13Paragraph":
      "Le terrain du sprint urbain est un petit village typique du sud de la Seine et Marne avec quelques petites ruelles ainsi que des quartiers rsidentiels offrant une vitesse de course plus leve. La carte possde galement plusieurs parcs ou prs qui ncessiteront aux coureurs de sadapter aux diffrents environnements.",
    "Home.box14Paragraph":
      "Les terrains de la moyenne distance et de la longue distance sont similaires et typiques des terrains de la fort de fontainebleau. La carte possde de nombreux chaos rocheux le plus souvent sur les flancs et des nombreuses collines sableuses qui rendent lorientation difficile. Entre ces zones dtailles, la visibilit est bien meilleure et un rseau de chemin assez dense devrait permettre une navigation plus aise.",
    "Home.box15ParagraphTitle": "Garderie: ",
    "Home.box15Paragraph":
      "Face au COVID-19, nous ne serons pas en mesure de mettre en place une garderie sur ces 3 jours, Merci de prendre vos dispositions. Il est possible en dernier recours pour les parents, de demander lors des inscriptions des heures de dpart dcales, nous ferons notre possible pour respecter vos souhaits compte tenu que nous devons aussi tenir compte des plages horaires par catgories.",
    "Home.box16ParagraphTitle": "Buvette: ",
    "Home.box16Paragraph1":
      "Sur nos 3 jours de course, vous trouverez la bire artisanale du gatinais 'La Gatine'. Le samedi et le dimanche sur l'arena, le foodtruck 'Sarazine' vous servira des crepes et des boissons fraiches. Et le dimanche, le traiteur 'Le Trady' vous proposera des boissons fraiches, des sandwich jambon-emmental  3 euros, des sandwich au thon  3,50 euros ou des plateaux repas  8 euros (au choix, taboul+cuisse de poulet+camembert+compote ou salade de coquillettes+roti de porc+camembert+compote). Pour les plateaux repas, veuillez les reserver avec ce ",
    "Home.box16Paragraph2": "lien",
    "Home.box17ParagraphTitle": "Inscriptions: ",
    "Home.box171Paragraph":
      "Les inscriptions aux 3 courses sont ouvertes sur le site de la ",
    "Home.box172Paragraph": " jusqu'au 7 aout.",
    "Home.box18ParagraphTitle": "Stands: ",
    "Home.box18Paragraph":
      "Prsence des stands partenaires AIR EXTREM et ORIENSPORT.",
